import { Component, OnInit } from '@angular/core';
import { HardloqrdemoService } from "../../hardloqrdemo.service";
import { QrDemo } from "../qr-demo";

@Component({
  selector: 'qr-compress-demo',
  templateUrl: './compress-demo.component.html',
  styleUrls: ['./compress-demo.component.less']
})
export class CompressDemoComponent implements OnInit {
  compressedBytes: number;
  compressedChar: number;
  uncompressedBytes: number;
  uncompressedChar: number;

  constructor(private svc: HardloqrdemoService) { }

  loading = false;
  sampleText = 'Our spy may use a new trap to fool the enemy. The agent in Havana works hard to pressure the union to elect a new boss. Rumor of our plot is minimal.';
  demo = new QrDemo('');

  ngOnInit() {
    this.getDemoOutput();
  }

  getDemoOutput(): void {
    this.loading = true;
    this.demo = new QrDemo(this.sampleText);
    this.svc.postDemo(this.demo)
      .subscribe(ret => {
        this.demo = ret;
        this.loading = false
        this.uncompressedChar = this.demo.summary.length;
        this.uncompressedBytes = this.demo.summary.length * 8;
        this.compressedChar = this.demo.compressedSummary.length;
        this.compressedBytes = this.demo.compressedSummary.length * 8;
      });
  }

  updateDemo(): void {
    this.getDemoOutput();
  }
}



