import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from "rxjs/Observable";
import { Qr } from "./qr/qr";
import { QrDemo } from "./demo/qr-demo";

@Injectable()
export class HardloqrdemoService {

  constructor(
    private http: HttpClient) { }

  private listUrl = 'http://localhost:8000/api/Qrs';
  private demoUrl = 'https://limitless-waters-31556.herokuapp.com/api/demo';

  getQrs(): Observable<Qr[]> {
    return this.http.get<Qr[]>(this.listUrl);
  };

  getQr(id: string): Observable<Qr> {
    const url = `${this.listUrl}/${id}`;
    return this.http.get<Qr>(url);
  }

  postQr(qr: Qr): Observable<Qr> {
    return this.http.post<Qr>(this.listUrl, qr);
  }

  putQr(qr: Qr): Observable<Qr> {
    const url = `${this.listUrl}/${qr._id}`;
    return this.http.put<Qr>(url, qr);
  }

  delQr(id: string): Observable<Qr> {
    const url = `${this.listUrl}/${id}`;
    return this.http.delete<Qr>(url);
  }

  postDemo(demo: QrDemo): Observable<QrDemo> {
    return this.http.post<QrDemo>(this.demoUrl, demo);
  }
}



